@import "https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap";
@import "https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap";
*, :after, :before {
  box-sizing: border-box;
}

#main-icon {
  border: thick solid;
  border-color: var(--accent-color, --default-accent-color);
  margin-bottom: 2rem;
  margin-inline: auto;
  border-radius: 100%;
  margin-top: 2rem;
  display: block;
}

#call-to-action > button {
  border: solid;
  border-color: var(--foreground);
  cursor: pointer;
  letter-spacing: -.01em;
  border-radius: .375rem;
  margin-inline: auto;
  padding: .6em 1em;
  font-family: Roboto, sans-serif;
  font-size: 1.2rem;
  line-height: 1.3;
  display: flex;
}

body {
  background-color: var(--background, --default-background);
  color: var(--foreground, --default-foreground);
  margin: 0;
  font-family: Libre Baskerville, serif;
  font-weight: 400;
  line-height: 2;
}

header {
  background-color: var(--non-specific-background, --default-background);
  padding: .5em;
  grid-template-rows: auto;
  grid-auto-flow: column;
  gap: 1em;
  margin-bottom: 10vh;
  padding-inline: 2rem;
  display: none;
}

footer {
  background-color: var(--non-specific-background, --default-background);
  margin-top: 2em;
}

#skip-to-content {
  background-color: var(--background, --default-background);
  border: 2px solid var(--non-specific-background);
  color: var(--accent-color, --default-accent-color);
  border-radius: .1rem;
  margin-top: .5rem;
  margin-left: .5rem;
  position: absolute;
  transform: translateX(-1000%);
}

a {
  color: var(--foreground, --default-foreground);
}

a:hover, a:focus-visible, a:focus {
  box-shadow: 0 0 0 2px var(--accent-color, --default-accent-color);
  color: var(--accent-color, --default-accent-color);
  outline: none;
}

::selection {
  background-color: var(--accent-color, --default-accent-color);
}

.dark-theme, body[color-theme="dark"] {
  --background: #1f1f1f;
  --foreground: #f1f1f7;
  --non-specific-background: #393939;
  --accent-color: #3f72f3;
}

.dev-theme, body[color-theme="dev"] {
  --background: #19c8c8;
  --foreground: #0a0a0a;
  --non-specific-background: #323232;
  --accent-color: #c80a09;
}

.light-theme, body[color-theme="light"] {
  --background: #f1f1f7;
  --foreground: #1f1f1f;
  --non-specific-background: #d1d1e4;
  --accent-color: #3f72f3;
}

main {
  max-width: 65ch;
  margin-inline: auto;
  padding: 1em 5ch;
}

nav {
  grid-template-rows: auto;
  grid-auto-flow: column;
  justify-self: end;
  justify-items: center;
  gap: 1em;
  padding-inline: 2rem;
  font-family: Roboto, sans-serif;
  display: grid;
}

footer p {
  text-align: center;
  margin-block: 0;
  font-family: Roboto, sans-serif;
  font-size: .8rem;
  font-style: italic;
  font-weight: 100;
}

#introduction {
  margin-top: 3em;
}

#introduction > h3 {
  text-align: center;
  margin-top: -1em;
  margin-bottom: 1em;
  font-style: italic;
  font-weight: 100;
}

h2 {
  margin-block: 1.3em;
}

header > h2 {
  font-style: bold;
  text-align: center;
  margin-block: 0;
}

#introduction > h1 {
  text-align: center;
  font-size: 2rem;
}

#call-to-action > h3 {
  text-align: center;
  font-style: italic;
  font-weight: 100;
}

#cv-header {
  --inline-padding: 1ch;
  --inline-height: .1em;
  text-align: center;
  display: flex;
}

.project-card {
  background-color: var(--background);
  max-width: 40ch;
  min-height: 30ch;
  border-radius: .5rem;
  padding-inline: 1ch;
  text-decoration: none;
  box-shadow: 3px 3px 5px #000000ab;
}

.project-card-title {
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  color: var(--foreground);
  text-transform: uppercase;
  background-color: #0000004d;
  margin-top: 40%;
  display: grid;
}

.project-card-title:after {
  background-color: var(--foreground);
  content: "";
  height: .2rem;
  border-radius: .2em;
}

.project-card-description {
  background-color: #fff;
  border-radius: .5em;
  margin-top: -.5em;
  padding: 1em;
}

#project-card-container {
  justify-content: center;
  gap: .5em;
  padding: 1em;
  display: grid;
}

#card-mentor {
  background-image: url("mentor_front_page.9147b82d.png");
  background-position: 70%;
  background-size: cover;
}

#card-mentor > h3 {
  color: var(--background);
}

#card-mentor > h3:after {
  background-color: var(--background);
}

#card-aoc {
  background-image: url("aoc_stars.6dff34cc.png");
  background-size: cover;
}

#card-aoc > h3 {
  color: var(--background);
}

#card-aoc > h3:after {
  background-color: var(--background);
}

#card-cstars {
  background-image: url("cstars_icon_dark_bg.08401367.png");
  background-position: 99%;
  background-size: cover;
}

#card-cstars > h3 {
  color: var(--background);
}

#card-cstars > h3:after {
  background-color: var(--background);
}

#card-dotfiles {
  background-image: url("dotfile_icon_dark_bg.e3d9528e.png");
  background-position: 80%;
  background-size: cover;
}

#card-dotfiles > h3 {
  color: var(--background);
}

#card-dotfiles > h3:after {
  background-color: var(--background);
}

h1, h2, h3 {
  font-family: Roboto, sans-serif;
  line-height: 1.2;
}

#skip-to-content:focus {
  transition: all .2s;
  transform: translateX(0);
}

html {
  scroll-behavior: smooth;
}

section > h2 {
  display: flex;
}

section > h2:after {
  background-color: var(--foreground);
  content: "";
  height: .1em;
  flex: auto;
  align-self: center;
  margin-left: 1ch;
}

#cv-header:after {
  background-color: var(--foreground);
  content: "";
  height: var(--inline-height);
  margin-left: var(--inline-padding);
  flex: auto;
  align-self: center;
}

#cv-header:before {
  background-color: var(--foreground);
  content: "";
  height: var(--inline-height);
  margin-right: var(--inline-padding);
  flex: auto;
  align-self: center;
}

